<template>
    <div class="wrap">
        <h1>{{msg}}</h1>
        <h1>5월 마지막주 영화 예매 순위</h1>
        <ul class="movies">
            <li v-for="movie in movies" class="item" :key="movie.id">
                <span class="rank">{{movie.id}}</span>
                <router-link :to="{ name: 'show', param: {id: movie.id}}">
                    <img v-bind:src="movie.poster" class="poster">
                </router-link>
                <div class="detail">
                    <strong class="tit">{{movie.name}}</strong><br>
                    <span class="rate">예매율
                        <span class="num">{{movie.rate}}</span><br>
                    </span>
                    <router-link :to="{name: 'show', params: {id: movie.id}}" class="link">자세히 보기</router-link>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'MovieIndexPage',
    props: {
        msg: {
            type: String
        }
    },
    data() {
        return {
            movies: []
        }
    },
    create() {
        axios.get('/api/movies')
        .then((response => {
            console.log('--------------------------------- response.data : ', response.data);
            this.movies = response.data
        }))
    }
}
</script>

<style scoped>
h1, h2 {
    font-weight: normal;
}
ul {
    list-style-type: none;
    padding: 0;
}
li {
    display: inline-block;
    margin: 0 10px;
}
a {
    color: #42b983;
}
</style>